.range {
  margin-top: 3px;
  padding: 0 25px;
  padding-bottom: 18px;
}

.range__controls {
  position: relative;
}

.range__price-controls {
  width: 100%;
  margin: 0 auto;
  margin-top: 13px;
  display: flex;
  justify-content: space-between;

  font-size: 14px;
}

.range__scale {
  position: relative;
  width: 100%;
  height: 1px;
  background-color: #f0eae4;
}

.range__bar {
  position: absolute;
  width: 49%;
  height: 1px;
  background-color: $brown;

  top: 0;
  left: 60px;
}

.range__toggle {
  position: absolute;
  top: -7px;

  width: 15px;
  height: 15px;

  border-radius: 50%;
  background-color: $brown;

  cursor: pointer;

  &--min {
    left: 45px;
  }

  &--max {
    right: 33px;
  }
}

.range__price-min {
  margin-left: 38px;

  input {
    background-color: transparent;
  }
}

.range__price-max {
  margin-right: 24px;
}

@media (max-width: $desktop-width) {
  .range {
    margin-top: 0;
    margin-bottom: 0;
    padding-bottom: 35px;
  }

  .range__scale {
    width: 304px;
    margin-left: 10px;
  }

  .range__bar {
    width: 158px;

    left: 78px;
  }

  .range__toggle {
    top: -9px;

    width: 18px;
    height: 18px;
  }

  .range__toggle--min {
    left: 70px;
  }

  .range__toggle--max {
    right: 128px;
  }

  .range__price-controls {
    width: 78%;
    margin-left: 0;
    margin-right: 0;
    font-size: 16px;

    margin-top: 15px;
  }

  .range__price-min {
    margin-left: 50px;
  }
}

@media (max-width: $tablet-width) {
  .range {
    margin-top: 7px;
    margin-bottom: 0;

    padding-bottom: 23px;
  }

  .range__toggle--min {
    left: 12%;
  }

  .range__scale {
    width: 112%;
    margin-left: -6%;
  }

  .range__bar {
    width: 58%;
    left: 17%;
  }

  .range__toggle--max {
    right: 18%;
  }

  .range__price-controls {
    width: 100%;
    margin-top: 17px;
    margin-left: auto;
    margin-right: auto;
  }

  .range__price-min {
    margin-left: 0;
    position: relative;
    left: 8%;
  }

  .range__price-max {
    margin-right: 0;
    position: relative;
    right: 14%;
  }
}
